body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue','Monaco','Copperplate','Brush Script MT',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
}

.home_page{
  height: 100%;
  width: 100%;
  float: left;
  background-color: navajowhite;
}

#nav_bar{
left: 100px;
height:50px;
background-color: brown;
float: right;
position: fixed;
border-radius: 25px;
z-index: 1000!important;
}

#nav_bar ul {
float:center;
padding:0; 
height:50px;
color: white;
display: inline-block;
}

#nav_bar li 
{
float: left;
color: white;
height:34px;
list-style: none;
padding: 13px 20px 0 20px;
}

#nav_bar li:hover{
height: 36px;
font-size: 20px;
background: orange;
text-align: justify;
border-radius: 25px;
margin: 10px;
}

#heading_box{
  position: absolute;
  left: 30%;
  top: 30%;
  text-decoration: navy;
  font-weight: 400;
  text-transform: uppercase;
}

#heading_hand{
  display: block;
  font-size: 120px;
  font-weight: 400;
  float: left;
  font-family: Perpetua;
  color: rosybrown;
}

#heading_in{
